import { React, useEffect, useState, useRef } from "react";
import { Text, Heading, Box, Flex, Center } from "@chakra-ui/react";
import { motion } from "framer-motion";

function NameChange() {
  const [count, setCount] = useState(1);
  const [name, setName] = useState("a Web Developer");
  const names = [
    "a Web Developer",
    "a Photographer",
    "an AI Enthusiast",
    "a Student",
    "a Programmer",
  ];

  const onUpdate = () => {
    setCount(count + 1);
    setName(names[count % names.length]);
  };

  const [color, setColor] = useState("#a3a3a3");
  const changeColor = () => {
    color === "#a3a3a3" ? setColor("#17CCF9") : setColor("#a3a3a3");
  };

  const words = useRef([
    "Hey,",
    "I'm",
    "Wei",
    "Kiat",
    name,
    "I",
    "can",
    "help",
    "YOU",
    "design",
    "solutions",
    "using",
    "technology",
  ]);

  const [highlightedWord, setHighlightedWord] = useState(8);

  useEffect(() => {
    const interval = setInterval(() => {
      setHighlightedWord(Math.floor(Math.random() * words.current.length));
    }, 3000);
    return () => clearInterval(interval);
  }, []);

  const handleHover = () => {
    setHighlightedWord(Math.floor(Math.random() * words.current.length));
  };

  const LightUpRandomWord = ({ word, index }) => {
    return (
      <>
        <Box
          as="span"
          color={highlightedWord === index ? "#17CCF9" : null}
          onMouseEnter={handleHover}
        >
          {word === "YOU" ? <strong>{word}</strong> : word}
        </Box>
        <Box as="span" whiteSpace="pre">
          {" "}
        </Box>
      </>
    );
  };

  return (
    <Box w="100vw">
      <Center>
        <Heading
          letterSpacing="5px"
          color={color}
          fontSize={{ base: "24px", sm: "32px", md: "48px" }}
        >
          <Flex pb="10%" flexWrap="wrap" justifyContent="center">
            {words.current.slice(0, 4).map((word, index) => (
              <LightUpRandomWord key={index} word={word} index={index} />
            ))}
          </Flex>
        </Heading>
      </Center>
      <Text
        bg="rgba(18,18,18,0.9)"
        fontSize="24px"
        letterSpacing="8px"
        align="center"
        pb="5%"
        px="5%"
      >
        <motion.p
          onUpdate={onUpdate}
          animate={{
            skewX: [-100, 100, 0],
            transition: { duration: 0.07, repeatDelay: 1.43, repeat: Infinity },
          }}
        >
          {name}.
        </motion.p>{" "}
        {words.current.slice(5).map((word, index) => (
          <LightUpRandomWord
            key={index + 5}
            word={word}
            index={index + 5}
          />
        ))}
      </Text>
    </Box>
  );
}

export default NameChange;
